<template>
    <div class="game-user-options">
        <input v-model="playerName" type="text" />
        <button class="btn btn-link" @click="addPlayer">
            Dodaj gracza
        </button>
    </div>
</template>
<script>
export default {
  name: 'PlayerAdd',
  methods: {
    /**
     * On button/enter press emits 'add-player' and reseting playerName to empty string
     */
    addPlayer() {
      this.$emit('add-player', this.playerName);
      this.playerName = '';
    }
  },
  data() {
    return {
      playerName: ''
    };
  }
};
</script>
<style lang="scss">
.game-user-options {
  //margin: 3rem -0.25rem -0.25rem -0.25rem;
  padding: 0.25rem;
  background-color: darken(#ffffff, 2%);
  display: flex;
}
</style>





