<template>
  <div id="app" class="app">
    <nav class="app-nav">
      <!-- nav.vue -->
      <router-link to="/" class="nav-item">
        <i class="mdi mdi-home-outline mdi-24px"></i>
        <span class="nav-description">Dom</span>
      </router-link>
      <router-link to="/about" class="nav-item">
        <i class="mdi mdi-lifebuoy mdi-24px"></i>
        <span class="nav-description">Info</span>
      </router-link>
      <router-link to="/collector" class="nav-item">
        <i class="mdi mdi-plus-circle-outline mdi-24px"></i>
        <span class="nav-description">Collect</span>
      </router-link>
    </nav>
    <main class="app-main">
      <router-view/>
    </main>
    <!-- <footer class="app-footer">      
    Serduszko
    </footer> -->
  </div>
</template>
<script>
export default {
  name: 'App'
};
</script>
<style lang="scss">
$main-color: #3f5267;
$main-color-lighten: lighten($main-color, 30%);
$main-color-darken: darken($main-color, 10%);

* {
  box-sizing: border-box;
}

.app {
  color: #333;
  display: grid;
  font-family: Oswald, sans-serif;
  font-size: 16px;
  font-weight: 400;
  grid-template-columns: 50px auto;
  grid-template-rows: auto 100px;
  //border-top: 8px solid $main-color;

  a {
    text-decoration: none;
  }
}

.app-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  grid-column: 1 / 1;
  grid-row: 1 / 1;
  height: 100vh;
  position: sticky;
  top: 0;
  left: 0;

  .nav-item {
    display: inline-block;
    width: 44px;
    height: 50px;
    text-align: center;
    line-height: 1;
    padding: 0.25rem;
    color: #74787b;
    transition: transform 0.1s ease-out;
    text-transform: lowercase;

    &.router-link-exact-active {
      .mdi {
        display: block;
        color: $main-color;
      }

      .nav-description {
        color: $main-color;
      }
    }

    &:hover {
      color: $main-color-darken;
      text-decoration: none;
      transform: translateY(-3px);

      .mdi::before {
        color: $main-color-darken;
      }
    }

    .mdi {
      display: block;
      color: #99a3aa;

      &::before {
        font-weight: 500;
      }
    }

    .nav-description {
      display: block;
      font-size: 0.8rem;
    }
  }
}

.app-main {
  grid-column: 2 / 2;
  grid-row: 1 / 1;
}

.app-footer {
  height: 0;
  background-color: #333;
  border-top: 1px solid red;
  grid-column: 1 / span 2;
  grid-row: 2 / 2;
}

@media screen and (max-width: 600px) {
  .app {
    grid-template-columns: auto;
    grid-template-rows: auto 60px;
  }

  .app-main {
    grid-column: 1;
    grid-row: 1;
    max-height: calc(100vh - 60px);
    overflow-y: scroll;
  }

  .app-nav {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 60px;
    bottom: 0;
    background-color: #fff;
    grid-column: 1;
    grid-row: 2;
    border-top: 1px solid #eee;
  }
}

//transitions
.flip-list-move {
  transition: transform 0.3s;
}

.slide-fade-enter-active,
.slide-fade-leave-active {
  transition: all 0.7s ease;
}

.slide-fade-enter,
.slide-fade-leave-to {
  opacity: 0;
}
</style>



