<template>
    <div>
      <h1>{{ val }}</h1>
      <dartboard @hit="hitted"></dartboard>      
    </div>
</template>

<script>
// @ is an alias to /src
import Dartboard from '@/components/dartboard.vue'

export default {
  name: "home",
  components: {
    Dartboard
  },
  methods: {
    hitted(val) {
      this.val = val;
    }
  },
  data() {
    return {
      val: 0
    }
  }
};
</script>



