<template>
  <div>
    <transition-group name="flip-list" tag="ul">
      <li class="user" v-for="(item, index) in playerList" :class="[{'active': item.isActive}, item.type]" :key="item.id" @click="showInfo(index)">
        <div class="user-info">
          <span class="user-position"></span>
          <span class="user-name">{{ item.name }}</span>
          <span class="user-score">{{ item.score }}</span>
        </div>
        <span class="user-remove" @click.stop="removePlayer(item.id)">x</span>
      </li>
    </transition-group>
  </div>
</template>
<script>
/**
 * Showing list of players.
 */
export default {
  name: 'PlayerList',
  props: {
    /**
     * List of players with all info (name, score...)
     */
    playerList: Array
  },
  data() {
    return {
      playerName: ''
    };
  },
  methods: {
    /**
     * On icon press emits 'remove-player' with id of selected
     */
    removePlayer(id) {
      this.$emit('remove-player', id);
    }
  }
};
</script>
<style lang="scss" scoped>
.user {
  background-color: #fff;
  display: flex;
  justify-content: space-around;
  align-content: center;
  align-items: center;
  padding: 0.25rem;
  position: relative;
  cursor: pointer;

  &:hover {
    background-color: darken(#ffffff, 3%);
  }

  &.active {
    border: 1px solid #3f5267;
    background-color: lighten(#3f5267, 60%);
    border-radius: 3px;
  }

  .user-remove {
    position: absolute;
    top: 0;
    right: 0;
    cursor: pointer;
    padding: 0.2rem 0.25rem;
    color: RGBA(0, 0, 0, 0.8);
  }
  .user-info {
    display: flex;
    justify-content: space-between;
    width: 100%;
    padding: 0.5rem;
    align-items: center;

    .user-name {
      display: block;
      flex: 1 0 auto;
      text-transform: uppercase;
    }

    .user-score {
      display: block;
      flex: 1 0 auto;
      text-align: right;
      color: lighten(#333354, 50%);
      text-shadow: 1px 1px 2px RGBA(0, 0, 0, 0.24);
    }

    .user-position {
      border-radius: 50%;
      width: 0.5rem;
      height: 0.5rem;
      margin-right: 0.5rem;
      background-color: #f1f1f1;
      box-shadow: 0 0 3px 0 #f1f1f1;
      border: 1px solid darken(#f1f1f1, 30%);

      &.gold {
        background-color: gold;
        box-shadow: 0 0 3px 0 gold;
        border: 1px solid darken(gold, 30%);
      }

      &.silver {
        background-color: silver;
        box-shadow: 0 0 3px 0 silver;
        border: 1px solid darken(silver, 30%);
      }

      &.bronze {
        background-color: #cd7f32;
        box-shadow: 0 0 3px 0 #cd7f32;
        border: 1px solid darken(#cd7f32, 30%);
      }

      &.notPlaying {
        background-color: #000;
        box-shadow: 0 0 3px 0 #000;
        border: 1px solid darken(#000, 30%);
      }
    }
  }
}
</style>
<docs>
    ```js
        <player-list :playerList="[{name: 'Grzegorz', score: '120', isActive: false, id: 2}, {name: 'Micha', score: '160', isActive: true, id: 1}, {name: 'Rafa', score: '159', isActive: false, id: 3}]"></player-list>
    ```

    ### Types
</docs>




