<template>
  <div>
    <h1>The '01 Game</h1>
    <h2>{{ score }}</h2>
    <dartboard @hit="hitted"></dartboard>
  </div>
</template>

<script>
// @ is an alias to /src
import Dartboard from '@/components/dartboard.vue';

export default {
  name: 'The01Game',
  components: {
    Dartboard
  },
  methods: {
    hitted(val) {
      let score = this.score;
      score -= val;
      if (score > 0) {
        this.score = score;
        return;
      }

      if (score < 0) {
        alert('Too low');
        return;
      }

      if (score === 0) {
        this.score = 0;
        alert('Winner');
        return;
      }
    }
  },
  data() {
    return {
      score: 301
    };
  }
};
</script>



