import Vue from "vue";
import Router from "vue-router";
import Home from "./views/Home.vue";
import About from "./views/About.vue";
import The01Game from "./views/The01Game.vue";
import Collector from "./views/Collector.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    // {
    //   path: "/",
    //   name: "home",
    //   component: Home
    // },
    {
      path: "/about",
      name: "about",
      component: About
    },
    {
      path: "/The01Game",
      name: "The01Game",
      component: The01Game
    },
    {
      path: "/",
      name: "Collector",
      component: Collector
    }
  ]
});



