<template>
    <div class="log">
        <h2>Log</h2>
        <ul class="card">
            <li v-for="item in log" :key="item.id" class="log-item" :class="item.type">
                <p class="log-content">{{ item.content }}</p>
                <p class="log-time">{{ item.logTime }}</p>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  name: 'Logs',
  props: {
    log: Array
  }
};
</script>
<style lang="scss" scoped>
.log {
  margin-top: 1rem;

  ul {
    max-height: 300px;
    overflow-y: scroll;

    :nth-child(2n) {
      background-color: #eee;
    }

    .log-item {
      padding: 0.5rem;
      margin: 0.25rem 0;
      line-height: 1.2;
      display: flex;
      justify-content: space-between;

      &.round-sum {
        text-align: center;
        background-color: #4f9962;
        border-radius: 3px;
        .log-time {
          color: #eee;
          text-shadow: 1px 1px 2px RGBA(0, 0, 0, 0.24);
        }
      }

      &.warning {
        background-color: #ff9800;
        .log-time {
          color: #eee;
          text-shadow: 1px 1px 2px RGBA(0, 0, 0, 0.24);
        }
      }

      &.info {
        background-color: #2196f3;
        .log-time {
          color: #eee;
          text-shadow: 1px 1px 2px RGBA(0, 0, 0, 0.24);
        }
      }

      &.danger {
        background-color: #f44336;
        .log-time {
          color: #eee;
          text-shadow: 1px 1px 2px RGBA(0, 0, 0, 0.24);
        }
      }

      .log-time {
        background-color: transparent;
        text-align: right;
        color: lighten(#333354, 40%);
        text-shadow: 1px 1px 2px RGBA(0, 0, 0, 0.24);
      }
    }
  }
}
</style>





