<template>
  <div class="game">
    <div class="game-main">
      <h1>
        Collector
      </h1>
    </div>
    <!-- <h2>{{ score }}</h2> -->
    <div class="game-dartboard">
      <h2>Tarcza</h2>
      <dartboard @hit="hitted"></dartboard>
    </div>
    <div class="game-user-list">
      <h2>Lista graczy</h2>
      <player-list :playerList="playerList" class="card" @remove-player="removePlayer"></player-list>
      <player-add @add-player="addPlayer"></player-add>
      <div class="game-options">
        <button class="btn">
          Nowa gra
        </button>
        <button class="btn">
          Zakocz gre
        </button>
        <button class="btn">
          Opcje
        </button>
      </div>

      <div class="game-options-more">
        <label for="rounds">Rundy</label>
        <input id="rounds" type="range" v-model="rounds" min="1" max="21" /> {{ rounds }}
      </div>

      <logs :log.sync="log"></logs>
    </div>
    <modal v-if="showModal" @close="showModal = false">
      <h3 slot="header">
        Koniec Gry
      </h3>
      <p slot="body">
        Wygrywa {{ this.playerList[0].name }} z iloci punktw rwn {{ this.playerList[0].score }}
      </p>
      <div slot="footer">
        <button class="btn" @click="newGame()">
          Nowa gra
        </button>
        <button class="btn" @click="showModal = false">
          Zakocz
        </button>
      </div>
    </modal>
    <modal v-if="info.show" @close="showInfo = false">
      <h3 slot="header">
        Informacje o graczu {{ info.name }}
      </h3>
      <p slot="body">
        Statystyki rzutw
        <table class="table table-striped table-hover" v-if="info.score.length !== 0">
          <thead class="thead-dark">
            <tr>
              <th>#</th>
              <th>1</th>
              <th>2</th>
              <th>3</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="i in 20" :key="i">
              <th>{{ i }}</th>
              <th>{{ info.score[i - 1].single }}</th>
              <th>{{ info.score[i - 1,1].double }}</th>
              <th>{{ info.score[i - 1,2].triple }}</th>
            </tr>
            <tr>
              <th>25</th>
              <th>{{info.score[21].single }}</th>
              <th>{{info.score[21].double }}</th>
              <th>{{info.score[21].triple }}</th>
            </tr>
          </tbody>
        </table>
        <span class="d-block text-center m-4" v-else>
          Nie oddano jeszcze rzutu.
        </span>
      </p>
      <div slot="footer">
        <button class="btn" @click="info.show = false">
          OK
        </button>
      </div>
    </modal>
    <modal v-if="info.show" @close="showInfo = false">
      <h3 slot="header">
        Informacje o graczu {{ info.name }}
      </h3>
      <div slot="body">

      </div>
      <div slot="footer">
        <button class="btn" @click="info.show = false">
          OK
        </button>
      </div>
    </modal>
  </div>
</template>

<script>
// @ is an alias to /src
import Dartboard from '@/components/dartboard';
import Modal from '@/components/modal';
import PlayerList from '@/components/player-list';
import PlayerAdd from '@/components/player-add';
import Logs from '@/components/logs';

class Player {
  constructor(name, id) {
    this.name = name;
    this.score = 0;
    this.id = id;
    this.throw = 0;
    this.isActive = false;
    this.isPlaying = true;
    let arr = [];
    for (let i = 1; i <= 22; i++) {
      arr.push({
        single: 0,
        double: 0,
        triple: 0
      });
    }
    this.info = arr;
    this.currentRound = 0;
    this.type = '';
  }
}

class Log {
  constructor(content, type = '') {
    let logTime = new Date();
    this.content = content;
    this.id = Math.random() * 100 * Math.random();
    this.type = type;
    this.logTime = `${logTime.toLocaleTimeString(
      'pl-PL'
    )} ${logTime.toLocaleDateString('pl-PL')}`;
  }
}

export default {
  name: 'Collector',
  components: {
    Dartboard,
    Modal,
    PlayerList,
    PlayerAdd,
    Logs
  },
  computed: {
    currentRank() {
      return this.playerList.sort((a, b) => {
        return b.score - a.score;
      });
    }
  },
  mounted() {
    this.playerList.push(new Player('Micha', this.playerList.length));
    this.playerList.push(new Player('Grzegorz', this.playerList.length));
    this.playerList.push(new Player('Rafa', this.playerList.length));
  },
  methods: {
    hitted(val, multipler) {
      this.playerList[this.currentPlayer].score += val * multipler;
      this.playerList[this.currentPlayer].currentRound += val * multipler;
      this.addInfo(val, multipler);
      this.addThrow();
    },
    addThrow() {
      this.playerList[this.currentPlayer].throw += 1;
      if (this.playerList[this.currentPlayer].throw === 3) {
        this.nextPlayer();
      }
    },
    nextPlayer() {
      this.playerList[this.currentPlayer].isActive = false;

      this.addLog(
        `${this.playerList[this.currentPlayer].name} rzuci w tej rundzie  ${
          this.playerList[this.currentPlayer].currentRound
        }`,
        'round-sum'
      );

      alert('Nastpny gracz');
      this.currentPlayer++;
      if (this.playerList[this.currentPlayer].isPlaying !== true) {
        this.currentPlayer++;
      }
      if (this.currentPlayer >= this.playerList.length) {
        nextPlayer();
      }
      this.playerList[this.currentPlayer].currentRound = 0;
      this.playerList[this.currentPlayer].isActive = true;
      this.playerList[this.currentPlayer].throw = 0;
    },
    addInfo(val, multipler) {
      var str = `${this.playerList[this.currentPlayer].name} rzuci ${val *
        multipler}`;
      this.log.unshift(new Log(str));

      if (val === 25) {
        if (multipler === 1) {
          this.playerList[this.currentPlayer].info[21].single++;
          return;
        }

        if (multipler === 2) {
          this.playerList[this.currentPlayer].info[21].double++;
          return;
        }
      }
      if (multipler === 1) {
        this.playerList[this.currentPlayer].info[val - 1].single++;
        return;
      }

      if (multipler === 2) {
        this.playerList[this.currentPlayer].info[val - 1].double++;
        return;
      }

      if (multipler === 3) {
        this.playerList[this.currentPlayer].info[val - 1].triple++;
        return;
      }
    },
    showInfo(index) {
      this.info.score = this.playerList[index].info;
      this.info.name = this.playerList[index].name;
      this.info.show = true;
    },
    addRound() {
      if (this.currentRound === this.rounds) {
        this.endGame();
      }
      this.currentRound++;
      this.addLog(`Runda ${this.currentRound}`, 'info');
      this.currentPlayer = 0;
    },
    endGame() {
      this.playerList.sort((a, b) => {
        return b.score - a.score;
      });
      this.showModal = true;
      //player ktry speni wynik wygrywa
    },
    newGame() {
      if (this.playerList.length > 0) {
        this.shuffle(this.playerList);
        this.currentRound = 1;
        this.currentPlayer = 0;
        this.playerList = this.playerList.map(el => {
          el.score = 0;
          el.throw = 0;
          el.isActive = false;
          return el;
        });
        this.showModal = false;
        this.playerList[0].isActive = true;
        this.playerAdd = false;
        this.log = [];
        this.addLog('Nowa gra', 'info');
      } else {
        alert('brakuje graczy');
      }
    },
    addPlayer(playerName) {
      if (playerName !== '') {
        let player = new Player(playerName, this.playerList.length);
        this.playerList.push(player);
      }
    },
    addLog(content, type) {
      this.log.unshift(new Log(content, type));
    },

    //TODO
    removePlayer(id) {
      let player = this.playerList.filter(el => {
        return el.id === id;
      });
      player[0].type = 'not-playing';
      let index = this.playerList.indexOf(player[0]);

      //this.playerList.splice(index, 1);
    },
    shuffle(a) {
      for (let i = a.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [a[i], a[j]] = [a[j], a[i]];
      }
      return a;
    }
  },
  data() {
    return {
      score: 0,
      rounds: 5,
      currentRound: 1,
      currentPlayer: 0,
      throw: 0,
      showModal: false,
      playerName: '',
      playerAdd: false,
      playerList: [],
      info: {
        show: false,
        name: 'Micha',
        score: []
      },
      log: []
    };
  }
};
</script>
<style lang="scss">
.card {
  box-shadow: 0px 4px 6px RGBA(0, 0, 0, 0.18);
  padding: 0.25rem;
  border-radius: 3px;

  &.border {
    border: 0;
    border-top: 6px solid #3f5267;
  }
}

h2 {
  font-size: 2rem;
  color: lighten(#3f5267, 40%);
  text-shadow: 1px 0px 1px RGBA(0, 0, 0, 0.18);
  margin-bottom: 1rem;
}

//components
.game {
  display: grid;
  grid-template-columns: 350px auto 600px;
  grid-template-rows: 50px auto 300px;
  grid-gap: 10px;
  justify-items: center;

  .game-main {
    grid-column: 1 / span 3;
    grid-row: 1 / span 3;

    h1 {
      align-items: center;
      display: inline-block;
      font-size: 2.5rem;
      letter-spacing: 1.4px;
      line-height: 1.1em;
      text-transform: uppercase;
      margin: 0;
      padding: 0.25rem 0.5rem;
      position: relative;
      text-align: center;
      width: 100%;
    }
  }

  .game-dartboard {
    grid-column: 1 / 3;
    grid-row: 2;
    text-align: center;
    max-height: 80vh;

    h2 {
      text-align: left;
    }
  }

  .game-user-list {
    grid-column: 3;
    grid-row: 2;
    list-style-type: none;
    width: 100%;
    padding: 0 2rem;
  }
}

@media screen and (max-width: 600px) {
  .game {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: 50px auto 300px;
    grid-gap: 0px;
    justify-items: center;

    .game-main {
      grid-column: 1;
      grid-row: 1;
    }

    .game-dartboard {
      grid-column: 1;
      grid-row: 2;
      text-align: center;
      min-width: 80vw;

      h2 {
        text-align: left;
      }
    }

    .game-user-list {
      grid-column: 1;
      grid-row: 3;
      list-style-type: none;
      width: 100%;
      padding: 0 2rem;
    }
  }
}
</style>
<style lang="scss" scoped>
</style>



